html, body, #root, .App, .Board {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
.Knight {
  font-size: 90px;
  cursor: pointer;
  font-weight: bold; }

.Square {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%; }

.Board {
  display: flex;
  flex-wrap: wrap; }


